.inside {
  width: 86%;
  height: 100vh;
  display: flex;
  flex-direction: column;

  .filterModal {
    height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    &__date {
      height: 200px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;

      &_item {
        width: 400px;
        height: 125px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        border-radius: 10px;
        box-shadow: 0 1px 1px rgba(0, 0, 0, .11),
        0 2px 2px rgba(0, 0, 0, .11),
        0 4px 4px rgba(0, 0, 0, .11),
        0 6px 8px rgba(0, 0, 0, .11),
        0 8px 16px rgba(0, 0, 0, .11);

        label {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          border: 1px solid black;
          border-radius: 10px;
          height: 50px;
          width: 150px;
          position: relative;

          span {
            text-align: center;
            width: 30px;
            height: 30px;
            font-size: 20px;
            position: absolute;
            top: -20px;
            left: 10px;
            background-color: white;
            color: black;
          }

          input {
            font-size: 16px;
            width: 140px;
            height: 45px;
            border: none;
            padding: 0 10px;
          }
        }
      }
    }
  }

  &_head {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-around;

    select {
      width: 200px;
    }

    &_icon {
      font-size: 30px;

      &.active {
        color: #196CFF;
      }
    }

    .add_btn {
      width: 200px;
      height: 50px;
      border: 2px solid #196CFF;
      border-radius: 10px;
      background-color: transparent;
      color: #196cff;
      font-size: 20px;
      font-weight: bold;
    }

    .search {
      width: 500px;
      height: 50px;
      border-radius: 30px;
      box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.42);
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-left: 20px;
      color: #888888;
    }

    .filter_btn {
      width: 250px;
      height: 50px;
      background-color: #196cff;
      color: white;
      border-radius: 10px;
      font-size: 20px;
      font-weight: bold;
      border: none;
    }

    .show {
      height: 50px;
      width: 100px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 30px;
    }
  }

  .table {
    width: 100%;
    padding: 30px;
    flex-wrap: wrap;

    &__item {
      height: 85px;
      cursor: pointer;

      &_checkbox {

      }
    }

    .card {
      width: 510px;
      height: 276px;
      background-image: linear-gradient(#196CFF, #001C4E);
      border-radius: 10px;
      color: white;
      margin-bottom: 10px;

      .info {
        width: 100%;
        height: 130px;
        padding: 10px;
        display: flex;
        flex-direction: column;
        gap: 20px;
        flex-wrap: wrap;
        color: #FFFFFF;
      }

      &_img {
        width: 100%;
        display: flex;
        padding: 20px;
        justify-content: space-between;

        .image {
          display: flex;
          gap: 30px;
          align-items: center;
          font-weight: bold;
          font-size: 20px;
        }

        img {
          width: 75px;
          height: 75px;
          border-radius: 50%;
        }

        i {
          font-size: 25px;
        }
      }
    }

    .image {
      width: 50px;
      height: 50px;
      border-radius: 50%;
    }

    .check {
      width: 30px;
      height: 20px;

    }
  }

  .table th, td {
    border-bottom: 1px solid #888888;
    padding: 15px;
    text-align: center;
  }

  th {
    color: #196cff;
    font-size: 22px;
  }

  tr:nth-child(even) {
    background-color: #E0EBFF;
  }
}