.personal {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__item {
    width: 60%;
    height: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    position: relative;

    &_inner {
      position: absolute;
      top: 32%;
      right: 5%;
      color: red;
    }

    &__inner {
      width: 40%;
      height: 15%;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      margin: 10px 0;
      position: relative;

      &_icon {
        position: absolute;
        font-size: 25px;
        right: 20px;
        bottom: 30px;
      }

      &__btn {
        width: 30%;
        height: 8%;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        position: absolute;
        bottom: 5%;
      }

      input {
        width: 100%;
        height: 60px;
        padding: 0 20px;
        font-size: 22px;
        border-radius: 10px;
      }
    }

    &:nth-child(1) {
      width: 100%;
      height: 10%;
      background-color: #196CFF;
      color: white;
      justify-content: center;
    }
  }

  .btn {
    color: white;
    width: 60%;
    height: 80%;
    font-size: 22px;
    background-color: #196CFF
  }

  .pending {
    width: 100%;
    height: 100%;
  }
}