.main {
  width: 86%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  gap: 30px;

  &_header {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.53);

    h2 {
      font-size: 40px;
    }
  }

  &_container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 780px;

    .portfolio {
      width: 880px;
      height: 780px;
      border-radius: 10px;
      box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.53);
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      padding: 35px;
      gap: 50px;
      flex-wrap: wrap;
      position: relative;

      .button {
        width: 75px;
        height: 75px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 25px;
        left: 25px;
      }

      .modal {
        width: 150px;
        height: 55px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        list-style: none;
        position: absolute;
        top: 50px;
        right: 50px;
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.53);
        padding: 10px 15px;

        li {
          cursor: pointer;
          width: 70px;
          display: flex;
          justify-content: space-around;
          margin: 15px 0;
        }
      }

      .icon {
        position: absolute;
        top: 20px;
        right: 15px;
        font-size: 40px;
      }

      .box {
        width: 370px;
        height: 200px;
        background-color: rgba(25, 108, 255, 0.3);
        color: white;
        display: flex;
        align-items: center;
        flex-direction: column;
        border-radius: 10px;
        font-size: 25px;
        font-weight: 700;
        position: relative;
        z-index: 3;

        &_back {
          position: absolute;
          bottom: 0;
          z-index: -1;
          width: 100%;
          background-color: #196cff;
          border-radius: 0 0 10px 10px;
        }
      }

      .percent {
        align-self: flex-end;
        padding: 30px;
      }
    }

    .profile {
      width: 510px;
      height: 720px;
      border-radius: 10px;
      background-color: white;
      box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.39);
      display: flex;
      flex-direction: column;
      gap: 30px;
      align-items: center;
      color: rgba(0, 0, 0, 0.6);

      &.parents {
        justify-content: space-evenly;
      }

      &.portfolio {
        justify-content: space-evenly;
      }

      &_img {
        width: 100%;
        height: 200px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        border-bottom: 1px solid rgba(128, 128, 128, 0.59);
        flex-direction: column;
        gap: 20px;

        .camera {
          right: 37%;
          bottom: 35%;
          position: absolute;
          width: 40px;
          height: 40px;
          border-radius: 50%;
          background-color: #0048e3;
          display: flex;
          justify-content: center;
          align-items: center;
          color: white;
          font-size: 22px;
        }

        img {
          width: 100px;
          height: 100px;
          border-radius: 50%;
        }
      }

      .information {
        overflow-y: scroll;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        gap: 10px;
        width: 427px;
        height: 400px;

        &_inner {
          width: 410px;
          display: flex;
          align-items: center;
          justify-content: flex-end;
        }

        &::-webkit-scrollbar {
          width: 5px;
        }

        &.parents {
          height: 550px;
        }

        &.portfolio {
          height: 500px;
          border: none;
          box-shadow: none;
          flex-wrap: nowrap;
          padding: 0;
        }

        .info {
          height: 40px;
          width: 410px;
          border-bottom: 1px solid rgba(0, 0, 0, 0.25);
          display: flex;
          align-items: center;
          justify-content: space-between;
        }
      }
    }
  }
}