.block {
  width: 86%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  overflow-y: scroll;
  padding: 10px;

  .title {
    width: 100%;
    height: 150px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 10px;
    padding-top: 10px;
  }
}

.cross {
  position: absolute;
  top: 26%;
  right: 16%;
  font-size: 28px;
  color: red;
}

.icon {
  display: flex;
  justify-content: space-between;
  position: relative;
}

.icon i {
  font-size: 30px;
}

.title h1 {
  width: 200px;
  font-size: 50px;
  border-bottom: 5px solid black;
  padding: 0 20px;
}

.plus i {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #196CFF;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 30px;
}

.plus {
  position: fixed;
  right: 75px;
  bottom: 70px;
}

.container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: relative;
}

.container .video {
  width: 50%;
  height: 500px;
  border: 1px solid black;
}

.container img {
  width: 50%;
  height: 500px;
  border: 1px solid black;
}

.container .text {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 100px;
  padding: 100px 20px;
  letter-spacing: 1px;
}

.text h1 {
  font-size: 40px;
}

.btn {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: absolute;
  bottom: 8%;
  right: 4%;
  background-color: #196CFF;
  border: none;
  font-size: 85px;
  color: white;
}

.change {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 25px;
}

.modalFill {
  width: 86%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Inter", sans-serif;
  gap: 100px;
  position: absolute;
  left: 269px;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.4);

  &__container {
    width: 70%;
    height: 50%;
    border-radius: 10px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    &__item {
      width: 100%;
      height: 80%;
      display: flex;
      align-items: center;

      &_inner {
        width: 45%;
        height: 90%;
      }

      .form {
        width: 50%;
        height: 90%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;

        select {
          width: 60%;
          padding: 10px;
          outline: none;
          font-size: 20px;
          border-radius: 10px;
        }

        input {
          width: 60%;
          padding: 10px;
          outline: none;
          font-size: 20px;
          border-radius: 10px;
        }

        textarea {
          width: 60%;
          height: 50%;
          padding: 10px;
          outline: none;
          font-size: 20px;
          border-radius: 10px;
        }
      }

      .video {
        width: 45%;
        height: 90%;
        border-radius: 10px;
        display: flex;
        justify-content: center;

        input {
          width: 90%;
          height: 15%;
          border-radius: 10px;
          font-size: 22px;
          padding: 0 10px;
        }
      }

      .img {
        width: 45%;
        height: 90%;
        background-color: rgba(0, 0, 0, 0.4);
        border-radius: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;

        .icon {
          font-size: 100px;
        }

        .imgInput {
          width: 100%;
          height: 100%;
          opacity: 0;
          position: absolute;
        }
      }
    }

    button {
      width: 18%;
      height: 10%;
      background-color: #196CFF;
      color: white;
      font-size: 25px;
      border: none;
      border-radius: 5px;
    }
  }
}

.modal {
  width: 200px;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  position: absolute;
  background-color: white;
  border-radius: 5px;
  right: 5%;
  bottom: -30%;
  z-index: 5;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.39);

  li {
    padding-left: 10px;
    width: 100%;
    height: 45%;
    display: flex;
    align-items: center;

    .icon {
      margin-right: 5px;
    }
  }
}

.forms {
  width: 100%;
  height: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 50px;
  padding: 50px;
}

.forms h1 {
  font-size: 40px;
  color: #196CFF;
  align-self: flex-start;
}

.forms form {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.selects {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.selects select {
  width: 495px;
  height: 60px;
  padding: 10px;
  outline: none;
  border-radius: 5px;
  border: 3px solid #868686;
}

select:focus {
  border: 3px solid #196CFF;
}

button {
  width: 350px;
  height: 70px;
  background-color: #323232;
  color: white;
  font-size: 35px;
  outline: none;
  border: 1px solid #323232;
}