.login {
  width: 100%;
  height: 100vh;
  display: flex;
  background-image: url("../../resources/images/Rectangle 27 (1).png");

  &__container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.4);

    &__item {
      width: 35%;
      height: 75%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-color: rgba(255, 255, 255, 0.8);
      border-radius: 20px;

      &__inner {
        width: 85%;
        height: 65%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;

        input {
          width: 90%;
          height: 12%;
          background-color: rgba(255, 255, 255, 0);
          border: 0;
          border-bottom: 2px black solid;
          font-size: 20px;
          padding: 0 10px;
        }

        .check {
          width: 90%;
          height: 25%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          position: relative;

          &__icon {
            position: absolute;
            right: 20px;
            top: 15px;
            font-size: 23px;
          }

          &__item {
            width: 100%;
            height: 50%;
            display: flex;
            justify-content: space-between;
            align-items: center;

            &__inner {
              width: 30%;
              height: 100%;
              display: flex;
              justify-content: space-around;
              align-items: center;

              input {
                background-color: rgba(255, 255, 255, 0.8);
              }
            }
          }

          input {
            width: 100%;
            height: 45%;
          }
        }

        button {
          width: 35%;
          height: 10%;
          font-size: 20px;
          background-color: #196CFF;
          border: none;
          color: white;
          border-radius: 10px;
        }
      }

      &:nth-child(2) {
        width: 55%;
        height: 100%;
        background-color: rgba(255, 255, 255, 0);
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;

        img {
          width: 70%;
          height: 90%;
        }
      }
    }
  }

  .active {
    color: #0048e3;
  }
}