.input {
  width: 390px;
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid black;
  padding: 0 10px;

  &.small {
    height: 34px;
  }

  &.inner {
    width: 390px;
    font-size: 18px;
    padding: 5px 10px;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.25);
  }

  input {
    outline: none;
    border-top: 0;
    border-right: 0;
    border-left: 0;
  }

  &__inner {
    width: 95%;
    padding: 5px 10px;
    font-size: 20px;
    height: 100%;
    opacity: 0;
    position: absolute;
    z-index: 2;

    &.basic {
      border-bottom: 1px solid #196cff;
      color: #196cff;
    }

    &.small {
      border-bottom: 1px solid rgba(0, 0, 0, 0.25);
    }

    &.active {
      border-bottom: 1px solid #19ff2c;
      color: #19ff2c;
    }
  }
}