.mainBanner {
  height: 10vh;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: space-around;

  &__foreign {
    width: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__middle {
    width: 50%;
    display: flex;
    justify-content: space-around;
    align-items: center;

    &__inner {
      color: white;
      list-style: none;
      font-size: 20px;
      text-decoration: none;

    }
  }

  .notActive {
    text-decoration: none;
    color: #ffffff70;
  }

  .active {
    text-decoration: none;
    color: #0048e3;
    text-shadow: white;
  }
}