.main {
  &_inside {
    width: 86%;
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    gap: 30px;

    .block {
      display: flex;
      justify-content: space-around;
      width: 100%;
    }

    .portfolio {
      width: 880px;
      height: 780px;
      border-radius: 10px;
      box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.53);
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 35px;
      gap: 50px;
      flex-wrap: wrap;
      //position: relative;

      .box {
        width: 370px;
        height: 200px;
        background-color: rgba(25, 108, 255, 0.3);
        color: white;
        display: flex;
        align-items: center;
        //justify-content: center;
        flex-direction: column;
        border-radius: 10px;
        font-size: 25px;
        font-weight: 700;
        position: relative;
        z-index: 3;

        .percent {
          align-self: flex-end;
          padding: 30px;
        }

        &_back {
          position: absolute;
          bottom: 0;
          z-index: -1;
          width: 100%;
          background-color: #196cff;
          border-radius: 0 0 10px 10px;
        }
      }
    }
  }

  &_head {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.53);

    h2 {
      font-size: 40px;
    }

    .filter_btn {
      width: 230px;
      height: 50px;
      background-color: #196cff;
      color: white;
      border-radius: 10px;
      border: none;
    }
  }
}