.alert {
  position: absolute;
  z-index: 3;
  top: -100%;
  right: 50%;
  transform: translate(34%);
  padding: 10px 30px;
  box-shadow: 0 0 10px 10px #19ff2c;
  color: #19ff2c;
  border-radius: 10px;
  background-color: white;

  &.active {
    transition: 1.5s;
    top: 20px;
  }

  h2 {
    text-shadow: 0 10px 10px #19ff2c;
  }
}