.modal {
  width: 86%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 0;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.4);

  &__inner {
    width: 500px;
    height: 400px;
    border-radius: 10px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    &.biggest {
      width: 930px;
      height: 830px;
    }

    &.bigger {
      width: 930px;
      height: 730px;
    }

    &.big {
      height: 700px;
    }

    &.large {
      height: 550px;
    }

    &.small {
      height: 250px;
    }
  }
}