.button {
  width: 200px;
  height: 55px;
  border-radius: 10px;
  font-size: 23px;
  margin-bottom: 10px;

  &.disabled {
    background-color: grey;
  }

  &.active {
    box-shadow: 0 0 15px 5px #196cff;
    text-shadow: 0 0 5px #196cff;
  }

  &.base {
    color: white;
    background-color: #196cff;
  }

  &.custom {
    color: black;
    background-color: white;
  }

  &.reverse {
    color: #196cff !important;
    background-color: white !important;
    border: 2px solid #196cff !important;
  }

  &.mini {
    width: 150px;
    height: 50px;
  }
}