.profile {
  width: 86.5%;
  height: 100vh;
  display: flex;
  font-family: "Inter", sans-serif;
  position: relative;
  overflow: hidden;

  .background {
    width: 100%;
    height: 280px;
    background-color: #196DFF;
    position: absolute;
    z-index: -1;
  }

  .buttons {
    position: absolute;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 50px;

    .icon {
      z-index: 2;
      color: white;
      border-radius: 5px;
      font-size: 50px;
    }
  }

  .btn {
    z-index: 2;
    width: 75px;
    height: 75px;
    border-radius: 50%;
    font-size: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .next {
    width: 100px;
    height: 100px;
    background-color: #196CFF;
    color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 40px;
    position: absolute;
    z-index: 2;
    right: 35px;
    bottom: 40%;
  }

  &_container {
    width: 100%;
    position: relative;
    display: flex;
    transition: 1s;
    left: 0;
    right: 0;

    &.first {
      position: absolute;
      left: -100%;
    }

    &.second {
      position: absolute;
      right: -100%;
    }

    .alert {
      transition-duration: 1s;
      top: -100%;
      right: 50%;
      transform: translate(26%);
      background-color: white;
      position: absolute;
      padding: 10px 30px;
      font-size: 30px;
      box-shadow: 0 0 10px 10px #19ff2c;
      color: #19ff2c;
      text-shadow: 0 10px 10px #19ff2c;
      border-radius: 10px;

      &.active {
        top: 20px;
      }
    }

    .profile_box {
      width: 510px;
      height: 720px;
      border-radius: 10px;
      background-color: white;
      box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.39);
      position: absolute;
      bottom: 80px;
      left: 30%;
      display: flex;
      flex-direction: column;
      gap: 30px;
      align-items: center;
      color: rgba(0, 0, 0, 0.6);

      .box_img {
        width: 100%;
        height: 200px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        border-bottom: 1px solid rgba(128, 128, 128, 0.59);
        flex-direction: column;
        gap: 20px;

        .i {
          width: 40px;
          height: 40px;
          display: flex;
          align-items: center;
          justify-content: center;
          color: white;
          background-color: #196DFF;
          border-radius: 50%;
          position: absolute;
          bottom: 39%;
          left: 56%;
          font-size: 20px;
        }
      }

      img {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        border: 2px solid #196dff;
      }

      i {
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        background-color: #196DFF;
        border-radius: 50%;
        position: absolute;
        bottom: 40%;
        left: 55%;
        font-size: 20px;
      }

      .information {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        gap: 10px;
        width: 410px;
        height: 400px;
      }

      .info {
        height: 40px;
        width: 410px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.25);
        display: flex;
        align-items: center;

        &.notFill {
          color: black;
          display: flex;
          justify-content: center;
          align-items: center;
        }

        &__input {
          border-bottom: 1px solid rgba(0, 0, 0, 0.25);
          border-top: none;
          border-left: none;
          border-right: none;
          padding: 5px 10px;
          font-size: 20px;
        }
      }
    }
  }

  .box {
    width: 1053px;
    height: 794px;
    border-radius: 10px;
    display: flex;
    gap: 30px;
    position: absolute;
    background-color: #FFFFFF;
    left: 20%;
    bottom: 20px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.66);
    padding: 20px;

    button {
      position: absolute;
      bottom: 10px;
      right: 50%;
      transform: translate(50%);
    }

    h2 {
      color: #196CFF;
      font-size: 35px;
      font-weight: bold;
    }

    .parents_information {
      width: 50%;
      display: flex;
      flex-direction: column;
      gap: 15px;

      .radio {
        display: flex;
        gap: 10px;
        align-items: center;
        color: #C6C6C6;

        input {
          width: 25px;
          height: 25px;
        }
      }
    }

    .info {
      width: 470px;
      padding: 15px;
      border-bottom: 1px solid #8C8C8C;

      &.notFill {
        color: black;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      input, select {
        width: 100%;
        border: none;
        outline: none;
      }
    }

    .portfolio {
      width: 50%;
      display: flex;
      flex-direction: column;
      gap: 15px;

      &.newList {
        overflow-y: scroll;
        height: 675px;
      }

      .file {
        width: 470px;
        padding: 13px;
        border-bottom: 1px solid #8C8C8C;
        display: flex;
        justify-content: space-between;

        i {
          color: #666666;
          font-size: 20px;
        }
      }
    }
  }


  .infos {
    width: 927px;
    height: 700px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.39);
    position: absolute;
    bottom: 80px;
    right: 50px;
    background-color: white;
    border-radius: 10px;
    padding: 30px;
    display: flex;
    flex-direction: row-reverse;
    color: rgba(0, 0, 0, 0.6);
    z-index: 2;

    h1 {
      color: #196dff;
    }

    .parents_information {
      width: 50%;
      display: flex;
      flex-direction: column;
      gap: 20px;


      .info {
        width: 400px;
        padding: 20px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.25);

        &__input {
          width: 90%;
          border-bottom: 1px solid rgba(0, 0, 0, 0.25);
          border-top: none;
          border-left: none;
          border-right: none;
          padding: 15px;
          font-size: 20px;
        }
      }
    }
  }

  .modal {
    width: 200px;
    height: 55px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    position: absolute;
    background-color: white;
    border-radius: 5px;
    right: 5%;
    bottom: 13%;
    z-index: 5;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.39);


    li {
      padding-left: 10px;
      width: 100%;
      height: 45%;
      display: flex;
      align-items: center;
    }
  }

  .change {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translate(-50%);
    width: 160px;
    height: 50px;
    background-color: #196CFF;
    color: white;
    font-size: 23px;
    border-radius: 10px;
  }

  .input_img {
    width: 40px;
    height: 40px;
    position: absolute;
    font-size: 0;
    background-color: rgba(255, 255, 255, 0);
    border: none;
    color: rgba(255, 255, 255, 0);
  }
}