.main {
  width: 100%;
  height: 100vh;
  font-family: "Inter", sans-serif;
  display: flex;
  justify-content: space-between;

  &__container {
    position: absolute;
    z-index: 4;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;

    &_item {
      width: 400px;
      height: 300px;
      background-color: white;
      border-radius: 20px;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
    }
  }
}

.box {
  width: 14%;
  height: 100%;
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.47);
  padding: 20px 0 20px 20px;
  display: flex;
  flex-direction: column;
  gap: 100px;
}

.box .head {
  display: flex;
  gap: 20px;
  align-items: center;
  color: #00000099;
}

.head img {
  border: 1px solid black;
  width: 45px;
  height: 45px;
  border-radius: 50%;
}

.head span {
  font-weight: 700;
}

.box .icon {
  display: flex;
  color: #00000099;
  gap: 10px;
}

.box .menu {
  display: flex;
  flex-direction: column;
  gap: 30px;
  height: 75%;
  font-weight: 700;
}

.exit {
  display: flex;
  gap: 10px;
  color: #00000099;
  padding: 10px;
  border-top: 1px solid #DCDCDC;
}

.icon i {
  font-size: 20px;
  color: #00000099;
}

.icon:hover {
  border-right: 5px solid #196CFF;
  color: #196CFF;
}

.icon:hover i {
  color: #196CFF;
}

.active {
  color: #196CFF;
  display: flex;
  align-items: center;
  text-decoration: none;
}

.notActive {
  color: black;
  display: flex;
  align-items: center;
  text-decoration: none;
}
