.main {
  &_inside {
    width: 86%;
    display: flex;
    flex-direction: column;
    gap: 30px;

    .form {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
    }

    .block {
      display: flex;
      justify-content: space-around;
      width: 100%;
    }

    .inside {
      height: 200px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;

      &__item {
        height: 70%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
      }
    }

    .portfolio {
      width: 880px;
      height: 800px;
      overflow-y: scroll;
      border-radius: 10px;
      box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.53);
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 35px;
      gap: 25px;
      color: #196cff;

      .file {
        display: flex;
        width: 600px;
        height: 40px;
        align-items: center;
        justify-content: space-between;
        border-bottom: 1px solid #196cff;
        padding: 5px;
        margin: 5px 0;

        &.active {
          border-bottom: 1px solid #19ff2c;
        }
      }

      h1 {
        align-self: flex-start;
        padding-left: 70px;
      }

      .add {
        width: 40px;
        height: 50px;
        border-radius: 50%;
        border: 1px solid #196cff;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 25px;
      }
    }
  }

  &_head {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.53);

    h2 {
      font-size: 40px;
      color: #196cff;
    }

    .filter_btn {
      width: 230px;
      height: 50px;
      background-color: #196cff;
      color: white;
      border-radius: 10px;
      border: none;
    }
  }
}