.main {
  width: 100%;
  height: 90vh;
  background-image: url("../../../resources/images/Rectangle 27 (1).png");
  background-size: cover;
  object-fit: cover;

  &__item {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    backdrop-filter: blur(2px);
    display: flex;
    align-items: flex-end;

    &__inner {
      width: 100%;
      height: 90%;
      display: flex;
      align-items: center;
      justify-content: flex-end;

      &__info {
        width: 30%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        color: white;

        p {
          margin: 20px 0;
        }

        button {
          width: 100%;
          height: 100%;
          font-size: 25px;
          font-family: Inter;
          border-radius: 10px;
        }
      }

      &__img {
        width: 60%;
        height: 100%;

        img {
          width: 85%;
          height: 95%;
          object-fit: cover;
        }
      }
    }
  }
}