.inside {
  width: 100%;
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  gap: 30px;

  .head {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.53);

    h2 {
      font-size: 40px;
    }
  }

  .block {
    display: flex;
    justify-content: space-around;
    width: 100%;
  }

  .portfolio {
    width: 880px;
    height: 770px;
    border-radius: 10px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.53);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 100px;
    flex-wrap: wrap;
    position: relative;

    &_item {
      position: absolute;
      top: 20px;
      left: 20px;
    }

    &_top {
      width: 100%;
      height: 247px;
      background-color: #196cff;
      border-radius: 10px 10px 0 0;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
      gap: 20px;

      h1 {
        color: white;
      }
    }

    .types {
      display: flex;
      gap: 100px;

      .radio {
        display: flex;
        align-items: center;
        gap: 10px;
        font-weight: bold;
        color: white;

        input {
          width: 20px;
          height: 20px;
        }
      }
    }

    .btn {
      width: 155px;
      height: 40px;
      border-radius: 10px;
      background-color: white;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #196CFF;
      align-self: flex-start;
      margin: 20px;
    }

    .inputs {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 50px;
      justify-content: center;
    }

    select, input {
      width: 430px;
      height: 40px;
      border-radius: 5px;
      outline: none;
      border: 1px solid #988C8C;
      padding: 10px;
    }

    button {
      width: 180px;
      height: 40px;
      border-radius: 5px;
      background-color: #196cff;
      color: white;
      font-weight: bold;
      border: none;
      font-size: 18px;
    }

  }

  .contact {
    width: 650px;
    height: 780px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.53);
    border-radius: 10px;
    display: flex;
    align-items: center;
    gap: 20px;
    flex-direction: column;
    padding: 25px;

    .box_img {
      width: 300px;
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 10px;

      img {
        width: 150px;
        height: 150px;
        border-radius: 50%;
        border: 2px solid black;
      }

      i {
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        background-color: #196DFF;
        border-radius: 50%;
        position: absolute;
        bottom: 22%;
        right: 28%;
        font-size: 20px;
      }

    }
  }

  .box {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;

    &__item {
      width: 100%;
      border-bottom: 1px solid #988C8C;
      padding: 10px;
      color: #988C8C;
    }

    button {
      margin: 10px;
      width: 150px;
      height: 40px;
      background-color: #196DFF;
      border-radius: 5px;
      border: none;
      color: white;
      font-size: 18px;
    }
  }
}




