.main {
  &_inside {
    width: 86%;
    display: flex;
    flex-direction: column;
    gap: 30px;

    .block {
      display: flex;
      justify-content: space-around;
      width: 100%;
    }

    .portfolio {
      width: 880px;
      height: 780px;
      border-radius: 10px;
      box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.53);
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      padding: 35px;
      gap: 20px;
      color: #196cff;

      input, select {
        width: 600px;
        height: 50px;
        border: none;
        border-bottom: 1px solid #196cff;
        outline: none;
        font-size: 18px;
        color: #196cff;


        &::placeholder {
          color: #196cff;
        }
      }

      button {
        width: 180px;
        height: 45px;
        border: none;
        color: white;
        outline: none;
        border-radius: 10px;
        background-color: #196cff;
        font-size: 18px;
      }

      .file {
        display: flex;
        width: 600px;
        height: 40px;
        align-items: center;
        justify-content: space-between;
        border-bottom: 1px solid #196cff;

        &.active {
          border-bottom: 1px solid #19ff2c;
        }

        input {
          border-bottom: 0;
        }
      }
    }
  }

  &_head {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.53);

    h2 {
      font-size: 40px;
      color: #196cff;
    }

    .filter_btn {
      width: 230px;
      height: 50px;
      background-color: #196cff;
      color: white;
      border-radius: 10px;
      border: none;
    }
  }
}