.inside {
  width: 86%;
  padding: 0 30px;
  display: flex;
  flex-direction: column;

  .filterModal {
    height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    &__date {
      height: 200px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;

      &_item {
        width: 400px;
        height: 125px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        border-radius: 10px;
        box-shadow: 0 1px 1px rgba(0, 0, 0, .11),
        0 2px 2px rgba(0, 0, 0, .11),
        0 4px 4px rgba(0, 0, 0, .11),
        0 6px 8px rgba(0, 0, 0, .11),
        0 8px 16px rgba(0, 0, 0, .11);

        label {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          border: 1px solid black;
          border-radius: 10px;
          height: 50px;
          width: 150px;
          position: relative;

          span {
            text-align: center;
            width: 30px;
            height: 30px;
            font-size: 20px;
            position: absolute;
            top: -20px;
            left: 10px;
            background-color: white;
            color: black;
          }

          input {
            font-size: 16px;
          }
        }
      }
    }

    &__payType {
      height: 150px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;

      &_item {
        width: 300px;
        height: 50px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        border-radius: 10px;
        box-shadow: 0 1px 1px rgba(0, 0, 0, .11),
        0 2px 2px rgba(0, 0, 0, .11),
        0 4px 4px rgba(0, 0, 0, .11),
        0 6px 8px rgba(0, 0, 0, .11),
        0 8px 16px rgba(0, 0, 0, .11);

        &_inner {
          display: flex;
          justify-content: center;
          align-items: center;

          input {
            margin-right: 5px;
            font-size: 30px;
          }
        }
      }
    }
  }
}

.head {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-around;

  select {
    width: 200px;
  }

  .search {
    width: 500px;
    height: 50px;
    border-radius: 30px;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.42);
    display: flex;
    align-items: center;
    padding-left: 20px;
    color: #888888;
  }

  .filter_btn {
    width: 250px;
    height: 50px;
    background-color: #196cff;
    color: white;
    border-radius: 10px;
    font-size: 20px;
    font-weight: bold;
    border: none;
  }
}

.table {
  width: 100%;

  &_delete {
    color: red;

    border: 1px solid red;
    border-radius: 20px;
    font-size: 25px;
    padding: 5px;
  }
}

.table th, td {
  border-bottom: 1px solid #888888;
  padding: 15px;
  text-align: center;
}

th {
  color: #196cff;
  font-size: 22px;
}

tr:nth-child(even) {
  background-color: #E0EBFF;
}

