.contact {
  width: 650px;
  height: 780px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.53);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
  flex-direction: column;
  padding: 25px;

  &_box {
    width: 500px;
    height: 200px;
    background-color: #E5E5E5;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border-radius: 10px;
    align-self: flex-start;

    .img {
      display: flex;
      align-items: center;
      gap: 20px;

      img {
        width: 85px;
        height: 85px;
        border-radius: 50%;
      }
    }

    .text {
      align-self: flex-end;
      width: 75%;
    }
  }

  .blue {
    align-self: flex-end;
    background-color: #E5EDFF;
  }


  h1 {
    color: #196cff;
  }

  .chat {
    width: 400px;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    border-radius: 40px;
    background-color: #E5E5E5;

    i {
      font-size: 20px;
      color: #919191;
    }

    input {
      outline: none;
      background-color: transparent;
      border: none;
    }

  }
}