.container {
  width: 86%;
  display: flex;
  flex-direction: column;
  padding-bottom: 20px;
  gap: 30px;

  .modalImg {
    width: 450px;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    &__item {
      width: 100%;
      height: 350px;
      display: flex;
      justify-content: space-around;
      align-items: center;

      img {
        width: 300px;
        height: 300px;
      }

      &_inner {
        width: 35px;
        height: 35px;
        font-size: 35px;
        z-index: 2;
        //background-color: black;
      }
    }
  }

  .box {
    width: 929px;
    height: 800px;
    background-color: #FFFFFF;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;

    &_selector {
      width: 100%;
      height: 150px;
      display: flex;
      justify-content: space-around;

      &_item {
        width: 50%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        padding: 10px;

        &.center {
          justify-content: space-around;
        }

        h3 {
          margin-bottom: 5px;
        }

        &_inner {
          width: 250px;
          height: 50px;

          &.scroll {
            overflow-y: scroll;
            overflow-x: hidden;
          }

          &.big {
            height: 100px;
          }

          &::-webkit-scrollbar {
            width: 0;
          }

          &_item {
            width: 250px;
            height: 45px;
            border: 1px solid black;
            border-radius: 10px;
            font-size: 22px;
            display: flex;
            justify-content: space-around;
            align-items: center;
            margin-bottom: 10px;
          }
        }
      }
    }

    &_inner {
      width: 410px;
      height: 100px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    form {
      width: 80%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 20px;


      input, textarea {
        width: 608px;
        height: 50px;
        background-color: #E7E7E7;
        border: 1px solid #868686;
        border-radius: 10px;
        outline: none;
        padding-left: 10px;
      }

      textarea {
        font-size: 20px;
        padding: 10px;
        height: 200px;

        &.active {
          height: 100px;
        }
      }

      .images {
        width: 83%;
        display: flex;
        flex-wrap: wrap;
        gap: 50px;
      }
    }
  }

  .addModal {
    height: 650px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    &__container {
      height: 300px;
      display: flex;
      flex-direction: column;

      &_item {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        height: 100px;

        &:nth-child(2) {
          height: 200px;
        }

        .inner {
          height: 175px;
          display: flex;
          flex-direction: column;
          justify-content: space-evenly;
        }

        &_inner {
          display: flex;
          justify-content: space-evenly;
          align-items: center;
          width: 250px;
          height: 45px;
          border: 1px solid black;
          border-radius: 10px;
          font-size: 22px;

          span {
            cursor: pointer;
          }
        }
      }
    }
  }

  .form {
    height: 350px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }

  .modal {
    width: 450px;
    height: 350px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border-radius: 5px;
    position: relative;

    &_img {
      width: 400px;
      height: 200px;
    }

    &_icon {
      font-size: 100px;
    }
  }

  .head {
    padding: 10px;
    width: 100%;
    display: flex;
    gap: 20px;

    &_inner {
      width: 50%;
      display: flex;
      justify-content: space-around;
      align-items: center;

      &_item {
        width: 500px;
        height: 50px;
        border-radius: 30px;
        box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.42);
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-left: 20px;
        color: #888888;
      }

      .i {
        font-size: 30px;
      }
    }

    .soha {
      background-color: #196CFF;
      border: 1px solid #196CFF;
      color: #FFFFFF;
    }

    .slide {
      width: 50px;
      height: 50px;
      border-radius: 5px;
      border: 1px solid #C2C2C2;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #C2C2C2;
      font-size: 20px
    }
  }

  .block {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    overflow-y: scroll;
    overflow-x: hidden;
    gap: 30px;

    .box {
      width: 363px;
      height: 469px;
      background-color: #FFFFFF;
      box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      padding: 20px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 20px;
    }

    .img {
      width: 333px;
      height: 221px;
      border-radius: 5px;
      position: relative;

      &.notActive {
        border: 1px solid black;
      }

      img {
        width: 100%;
        height: 100%;
      }

      &_inner {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 5px;
        border-radius: 5px;
        width: 50px;
        height: 35px;
        color: rgba(255, 255, 255, 0.6);
        background-color: rgba(0, 0, 0, 0.56);
        position: absolute;
        right: 10px;
        top: 5px;

        &_item {
          font-size: 23px;
          color: white;
        }
      }

      &_item {
        width: 100%;
        bottom: 0;
        position: absolute;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px;
        color: #FFFFFF;

        &_inner {
          color: black;
          font-size: 30px;
        }
      }

      i {
        font-size: 20px;
      }

      .icon {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 5px;
        border-radius: 5px;
        width: 60px;
        height: 35px;
        color: rgba(255, 255, 255, 0.6);
        background-color: rgba(0, 0, 0, 0.56);

        &_inner {
          font-size: 23px;
        }
      }
    }
  }

  &_info {
    display: flex;
    flex-direction: column;
    gap: 20px;

    span {
      color: #196CFF;
      font-weight: 700;
    }
  }

  .percent {
    font-size: 25px;
    color: #196CFF;
    font-weight: 700;
    align-self: flex-end;
  }

  .picture {
    border: 1px solid black;
    width: 280px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;

    &.active {
      width: 400px;
      height: 200px;
    }

    img {
      width: 100%;
      height: 100%;
    }
  }
}


