.input {
  width: 410px;
  height: 40px;
  display: flex;
  justify-content: center;

  &.small {
    height: 34px;
  }

  &.inner {
    width: 390px;
    font-size: 18px;
    padding: 5px 10px;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.25);
  }

  &.hidden {
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid black;
  }

  &.search {
    width: 460px;
    border: none;
  }

  input {
    outline: none;
    border-top: 0;
    border-right: 0;
    border-left: 0;
  }

  &__inner {
    width: 95%;
    padding: 5px 10px;
    font-size: 20px;
    color: black;

    &.search {
      border: none;
    }

    &.hidden {
      opacity: 0;
      position: absolute;
      z-index: 2;
    }

    &.basic {
      border-bottom: 1px solid #196cff;
      color: #196cff;
    }

    &.small {
      border-bottom: 1px solid rgba(0, 0, 0, 0.25);
    }

    &.active {
      border-bottom: 1px solid #19ff2c;
      color: #19ff2c;
    }
  }
}