.basic {
  width: 86%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Inter", sans-serif;
  gap: 50px;
  background-color: #E8F0FF;
  flex-wrap: wrap;
  padding: 20px 0;

  .inside {
    height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    &__item {
      width: 90%;
      height: 100px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;

      &_inner {
        width: 100%;
        height: 40%;
        margin: 10px;
        padding: 10px 20px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        border: 2px solid black;
        border-radius: 10px;
        font-size: 24px;

        .icon {
          color: red;
        }
      }
    }
  }
}

.basic .menu {
  width: 257px;
  height: 100%;
  box-shadow: 0 0 10px black;
}

.basic .box {
  width: 460px;
  height: 650px;
  border-radius: 10px;
  border: 1px solid #CCCCCC;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  background-color: #FFFF;

  &.eval {
    height: 750px;
  }

  &_icon {
    position: absolute;
    top: 3%;
    left: 5%;
    font-size: 20px;
  }

  .before {
    position: absolute;
    top: 0;
    right: -22%;
    width: 220px;
    height: 100px;
    transform: rotate(50deg);
  }

  .after {
    position: absolute;
    bottom: 0;
    left: -22%;
    width: 220px;
    height: 100px;
    transform: rotate(50deg);
  }
}

.box ul {
  display: flex;
  flex-direction: column;
  gap: 12px;
  justify-content: center;
  padding: 50px;
}

.box button {
  width: 209px;
  height: 50px;
  background-color: #323232;
  color: white;
  border: none;
  outline: none;
  border-radius: 5px;
  font-size: 20px;
}

.box .circle {
  width: 170px;
  height: 170px;
  border-radius: 50%;
  background-color: #323232;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 32px;
  flex-direction: column;
  position: relative;

  &_item {
    position: absolute;
    bottom: 20%;
    right: 10%;
    display: flex;
    flex-direction: column;

    .mini {
      font-size: 16px;
    }
  }
}

.box span {
  font-size: 16px;
  align-self: flex-end;
  padding: 10px;
}

.btn {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: absolute;
  bottom: 8%;
  right: 4%;
  background-color: #196CFF;
  border: none;
  font-size: 85px;
  color: white;
}

.modalAdd {
  width: 86%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 0;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.4);

  &__container {
    width: 25%;
    border-radius: 10px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding: 15px 10px;

    &__item {
      width: 90%;
      height: 60%;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;

      .selectedSubs {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;

        &__item {
          width: 150px;
          display: flex;
          justify-content: space-around;
          align-items: center;
          border: 1px solid black;
          border-radius: 10px;
          padding: 5px;
        }
      }

      input, select {
        width: 90%;
        height: 50px;
        font-size: 20px;
        padding: 0 10px;
        border-radius: 5px;
        margin: 10px;
      }

      .inner {
        width: 100%;
        display: flex;
        justify-content: space-around;

        &__item {
          display: flex;

          input {
            width: 20px;
            height: 20px;
            margin-left: 10px;
          }
        }
      }
    }
  }
}